import React, {Component} from 'react';
import {network} from '../../core/config.js'
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as Actions from '../../redux/actions.js';
import './Sale.css';

class Sale extends Component {
	constructor() {
		super();
		this.state = {
			account: {
				name: '', authority: '', owner: true
			}, price: '', useraccount: '', showSaleTab: false, showControl: false
		}
		document.title = '';
	}

	componentWillReceiveProps(nextProps) {
		let scatter = nextProps.scatter;
		let eos = nextProps.eos;
		let _this = this;
		if (scatter && eos) {
			scatter.getIdentity({accounts: [network]}).then((identity) => {
				let account = identity.accounts[0];
				let options = {
					authorization: account.name + '@' + account.authority, broadcast: true, sign: false
				};
				eos.getAccount(account.name).then(data => {
					console.log(data)
					let saleAccount = this.props.namelist.find((name) => name.name === data.account_name);
					_this.setState({
						showControl: true,
						price: saleAccount ? saleAccount.price * 0.0001 : '',
						useraccount: saleAccount ? saleAccount.username : '',
						account: {
							name: data.account_name,
							authority: account.authority,
							owner: data.permissions[1].required_auth.accounts[0]?(data.permissions[1].required_auth.accounts[0].permission.actor === 'eosnamedapp1'):false
							// public_key: data.permissions[0].required_auth.keys[0].key
							//permissions[0].required_auth.keys[0].key
						}
					})
				})
			}).catch((e) => {
				if (e.code === 423) {
					alert('Scatter is locked, please unlock it so that you can login with Scatter')
				}
			})
		}
	}

	componentDidMount() {

		let scatter = this.props.scatter;
		let eos = this.props.eos;
		let _this = this;
		if (scatter && eos) {
			scatter.getIdentity({accounts: [network]}).then((identity) => {
				let account = identity.accounts[0];
				eos.getAccount(account.name).then(data => {
					let saleAccount = this.props.namelist.find((name) => name.name === data.account_name);
					this.setState({
						showControl: true,
						price: saleAccount ? saleAccount.price * 0.0001 : '',
						useraccount: saleAccount ? saleAccount.username : '',
						account: {
							name: data.account_name,
							authority: account.authority,
							owner: data.permissions[1].required_auth.accounts[0]?(data.permissions[1].required_auth.accounts[0].permission.actor === 'eosnamedapp1'):false
							// public_key: data.permissions[0].required_auth.keys[0].key
						}
					})
				})
			}).catch((e) => {
				if (e.code === 423) {
					alert('Scatter is locked, please unlock it so that you can login with Scatter')
				}
			})
		}
	}

	authorize = () => {
		console.log('test')
		let eos = this.props.eos;
		let scatter = this.props.scatter;
		if (scatter && eos) {
			scatter.getIdentity({accounts: [network]}).then((identity) => {
				let account = identity.accounts[0];
				let options = {
					authorization: account.name + '@' + account.authority, broadcast: true, sign: true
				};
				if (account.authority=== 'owner') {
					eos.contract('eosnamedapp1', options).then((contract) => {
						eos.transaction({
							delay_sec: 60, actions: [{
								account: 'eosio',  //
								name: 'updateauth',               //
								authorization: [{
									actor: account.name, permission: account.authority
								}], data: {

									"permission": "owner", "parent": "", "account": account.name, "auth": {
										"accounts": [{
											"permission": {"actor": "eosnamedapp1", "permission": "eosio.code"},
											"weight": 1
										}], "waits": [], "keys": [], "threshold": 1
									}
								}
							}]
						}).then(()=>{
							window.location.reload();
						}).catch((e)=>{
							let err = JSON.parse(e);
							if(err.error.code===3080004){
								alert('')
							}
						})
					})
				} else {
					alert('ownerScatter')
				}
			})
		}


	}
	createOrder = () => {
		if (!this.state.account.name) {
			alert('scatter');
		} else if (!this.state.price) {
			alert('0.0001EOS');
		} else if (!this.state.useraccount) {
			alert('');
		} else {
			let eos = this.props.eos;
			let scatter = this.props.scatter;
			if (scatter && eos) {
				scatter.getIdentity({accounts: [network]}).then((identity) => {
					let account = identity.accounts[0];
					let options = {
						authorization: account.name + '@' + account.authority, broadcast: true, sign: true
					};
					if(!this.state.account.owner){
						alert('owner');
					}else if (account.authority === 'active') {
						eos.getAccount(this.state.useraccount).then(data => {
							//:1.owner2.addbill
							eos.contract('eosnamedapp1', options).then((contract) => {
								eos.contract('eosnamedapp1').then(myaccount => {
									eos.transaction({
										actions: [{
											account: 'eosnamedapp1',  //
											name: 'addbill',               //
											authorization: [{
												actor: account.name, permission: account.authority
											}], data: {
												username:this.state.useraccount,//
												name:account.name,
												price:this.state.price*10000,
												precision:4
											}
										}]
									}).then(result => {
										alert('');
										window.location.reload();
									}).catch((e)=>{
										let err = JSON.parse(e);
										if(err.error.code===3080004){
											alert('')
										}
									})
								})
							})
						}).catch(e => {
							alert('')
						})
					} else {
						alert('activeScatter')
					}
				}).catch((e) => {
					if (e.code === 423) {
						alert('Scatter is locked, please unlock it before you can login with it')
					}
				})
			}
		}
	}
	editOrder = () => {
		if (!this.state.account.name) {
			alert('scatter');
		} else if (!this.state.price) {
			alert('0.0001EOS');
		} else if (!this.state.useraccount) {
			alert('');
		} else {
			let eos = this.props.eos;
			let scatter = this.props.scatter;
			if (scatter && eos) {
				scatter.getIdentity({accounts: [network]}).then((identity) => {
					let account = identity.accounts[0];
					let options = {
						authorization: account.name + '@' + account.authority, broadcast: true, sign: true
					};
					if(!this.state.account.owner){
						alert('owner');
					}else if (account.authority === 'active') {
						eos.getAccount(this.state.useraccount).then(data => {
							//:1.owner2.addbill
							eos.contract('eosnamedapp1', options).then((contract) => {
								eos.contract('eosnamedapp1').then(myaccount => {
									eos.transaction({
										actions: [{
											account: 'eosnamedapp1',  //
											name: 'updatebill',               //
											authorization: [{
												actor: account.name, permission: account.authority
											}], data: {
												username:this.state.useraccount,//
												name:account.name,
												price:this.state.price*10000,
												precision:4
											}
										}]
									}).then(result => {
										alert('');
										window.location.reload();
									}).catch((e)=>{
										if(e.error.code===3080004){
											alert('')
										}
									})
								})
							})
						}).catch(e => {
							alert('')
						})
					} else {
						alert('activeScatter')
					}
				}).catch((e) => {
					if (e.code === 423) {
						alert('Scatter is locked, please unlock it before you can login with it')
					}
				})
			}
		}
	}
	returnKeys = ()=> {
		let eos = this.props.eos;
		let scatter = this.props.scatter;
		if (scatter && eos) {
			scatter.getIdentity({accounts: [network]}).then((identity) => {
				let account = identity.accounts[0];
				let options = {
					authorization: account.name + '@' + account.authority, broadcast: true, sign: false
				};
				if (account.authority === 'active') {
					eos.contract('eosnamedapp1').then(myaccount => {
						eos.getAccount(account.name).then(data => {
							eos.transaction({
								actions: [{
									account: 'eosnamedapp1',  //
									name: 'returnkeys',               //
									authorization: [{
										actor: account.name, permission: account.authority
									}], data: {
										account:account.name,
										public_key_str:data.permissions[0].required_auth.keys[0].key
									}
								}]
							}).then(result => {
								alert('');
								window.location.reload();
							}).catch((e)=>{
								console.log(JSON.parse(e))
								let err = JSON.parse(e);
								if(err.error.code===3080004){
									alert('')
								}
							})
						})

					})
				} else {
					alert('activeScatter')
				}
			}).catch((e) => {
				if (e.code === 423) {
					alert('Scatter is locked, please unlock it before you can login with it')
				}
			})
		}

	}
	deleteOrder = () => {
		let eos = this.props.eos;
		let scatter = this.props.scatter;
		if (scatter && eos) {
			scatter.getIdentity({accounts: [network]}).then((identity) => {
				let account = identity.accounts[0];
				let options = {
					authorization: account.name + '@' + account.authority, broadcast: true, sign: false
				};
				if (account.authority === 'active') {
					eos.contract('eosnamedapp1').then(myaccount => {
						eos.getAccount(account.name).then(data => {
							eos.transaction({
								actions: [{
									account: 'eosnamedapp1',  //
									name: 'deletebill',               //
									authorization: [{
										actor: account.name, permission: account.authority
									}], data: {
										account:account.name,
										public_key_str:data.permissions[0].required_auth.keys[0].key
									}
								}]
							}).then(result => {
								alert('');
								window.location.reload();
							}).catch((e)=>{
								console.log(JSON.parse(e))
								let err = JSON.parse(e);
								if(err.error.code===3080004){
									alert('')
								}
							})
						})

					})
				} else {
					alert('activeScatter')
				}
			}).catch((e) => {
				if (e.code === 423) {
					alert('Scatter is locked, please unlock it before you can login with it')
				}
			})
		}

	}

	render() {
		let saleAccount = this.props.namelist.find((name) => name.name === this.state.account.name);
		let isOnsale = !!saleAccount;
		return (
			<div className="buy-super-container">
				<div className="buy-container col-lg-6 col-xs-12">
					<div className="buy-account-container">
						<div className="buy-subline">
							<div style={{color: '#808080', fontWeight: 300}}></div>
							<div style={{marginLeft: '1rem', fontWeight: 600}}>{this.state.account.name}</div>
						</div>
						<div className="buy-subline">
							<div className="buy-subline-name">owner</div>
							<div
								onClick={this.state.showControl?(!this.state.account.owner?this.authorize : this.returnKeys) : () => {return false}}
								style={{textDecoration: 'underline', color: '#009ad6'}}
							>{this.state.showControl?(this.state.account.owner ? '' : '') : ''}</div>
						</div>
						<div className="buy-subline">
							<div className="buy-subline-name"></div>
							<div onClick={() => {
								this.state.showControl ? (this.setState({showSaleTab: !this.state.showSaleTab})) : ''
							}} style={{
								textDecoration: 'underline',
								color: '#009ad6'
							}}>{this.state.showControl ? (isOnsale ? '' : '') : ''}</div>
						</div>
					</div>
					<div style={{display: this.state.showSaleTab ? 'block' : 'none'}} className="buy-account-container">
						<div className="buy-subline">
							<div className="buy-subline-name"></div>
							<div style={{marginLeft: '1rem', fontWeight: 600}}>{this.state.account.name}</div>
						</div>
						<div className="buy-subline">
							<div className="buy-subline-name">
								<p style={{margin: 0}}>
									(EOS)
								</p>
								<p style={{margin: 0, fontSize: '1rem', color: '#808080'}}>0.0001EOS</p>
							</div>
							<input
								onInput={(e) => this.setState({price: e.target.value})}
								value={this.state.price}
								className="buy-subline-value"
								type="number"
								step={0.0001}
								min={0.0001}
								placeholder=""/>
						</div>
						<div className="buy-subline">
							<div className="buy-subline-name"></div>
							<input
								onInput={(e) => this.setState({useraccount: e.target.value})}
								className="buy-subline-value"
								value={this.state.useraccount}
								placeholder=""/>
						</div>
						<div className="buy-subline" style={{display: isOnsale ? 'flex' : 'none'}}>
							<button
								onClick={this.deleteOrder}
								className="btn btn-primary btn-lg ">
								
							</button>
							<button
								onClick={this.editOrder}
								className="btn btn-primary btn-lg ">
								
							</button>
						</div>
						<button style={{display: !isOnsale ? 'block' : 'none'}}
						        onClick={this.createOrder}
						        className="btn btn-primary btn-lg btn-block">
							
						</button>
					</div>
					<p style={{fontSize: '1rem', fontWeight: 300}}>
						1.owner(ownerScatter)<br/>
						<span style={{color: 'red'}}>{this.state.account.owner ? '' : ''}</span>
					</p>
					<p style={{fontSize: '1rem', fontWeight: 300}}>
						2.Scatteractive<br/>
						<span style={{color: 'red'}}>{this.state.account.authority === 'active' ? this.state.account.authority + '' : (this.state.account.authority ? this.state.account.authority + 'active' : 'Scatter')}</span>
					</p>
					<p style={{fontSize: '1rem', fontWeight: 300}}>3.//</p>
				</div>
			</div>
		)
	}
}

function mapStateToProps(state) {
	return {
		scatter: state.scatter.scatter,
		eos: state.eos.eos,
		namelist: state.namelist.namelist || []
	}
}

function mapDispatchToProps(dispatch) {
	return {
		actions: bindActionCreators(Actions, dispatch)
	}
}

export default connect(mapStateToProps, mapDispatchToProps)(Sale)


